<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">
      <router-link class='el-button--text' to="/information/list" replace>资讯管理</router-link>
      / {{pageTitle}}
    </el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" label-width="90px" label-suffix=":" label-position="left">
        <el-form-item label="资讯标题" prop="title" required>
          <el-input v-model="form.title" placeholder="请输入资讯标题" />
        </el-form-item>
        <el-form-item label="资讯描述" prop="desc" required>
          <el-input v-model="form.desc" placeholder="请输入资讯描述" />
        </el-form-item>
        <el-form-item label="资讯图片" required>
          <div v-if="form.image && form.image.url" class="avatar">
            <img :src="'https://szebra.cn' + form.image.url" />
            <i class="el-icon-circle-close" @click="removeConverImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="阅读基数" prop="base_number">
          <el-input v-model="form.base_number" />
        </el-form-item>
        <el-form-item label="资讯类别" prop="link_type" required>
          <el-radio-group v-model="form.link_type">
            <el-radio label="article">链接公众号文章</el-radio>
            <el-radio label="page">自定义页面</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.link_type == 'article'" label="文章地址" required>
          <el-input v-model="form.articleLink" placeholder="请输入公众号文章地址" />
        </el-form-item>
        <el-form-item v-else label="链接页面" required>
          <el-select v-model="form.pageId" placeholder="请选择链接页面">
            <el-option v-for="item in pageList" :key="item.id" :label="item.name" :value="item.id + ''" />
          </el-select>
        </el-form-item>
        <el-form-item v-if="submitFuncName === 'informationCreate'" label="发布时间" prop="public_at" required>
            <el-date-picker
              v-model="form.public_at"
              align="right"
              type="date"
              placeholder="选择日期"
              :picker-options="pickerOptions1" />
        </el-form-item>
        <el-button type="primary" @click="onSubmit">{{submitBtnTitle}}</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>
const G_IMAGE_SIZE = 2 // 图片大小

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      informationId: '', // 页面id
      pageTitle: '', // 页面标题
      submitBtnTitle: '',
      form: {
        id: '',
        title: '',
        desc: '',
        position: '',
        image: {
          url: ''
        },
        link_type: 'article', // default
        link: '',
        public_at: '',
        base_number: 0,
        pageId: '',
        articleLink: ''
      },
      pageList: [],
      pickerOptions1: {
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }]
      }
    }
  },

  methods: {
    formatSubmitValue () {
      if (this.form.link_type === 'page') this.form.link = this.form.pageId
      if (this.form.link_type === 'article') this.form.link = this.form.articleLink

      if (!this.form.image.id) {
        this.$message.error('图片不可为空')
        return
      }
      if (!this.form.link) {
        this.$message.error('链接不可为空')
        return
      }
      const ret = { id: this.form.id, information: Object.assign({}, this.form) }
      ret.information.pageId = ret.information.image = undefined
      ret.information.image_attributes = { id: this.form.image.id }
      return ret
    },
    articleLinkInputChange (link) {
      this.form.link = link
    },
    pagePickChange (link) {
      this.form.link = link
    },
    submit () {
      const data = this.formatSubmitValue()
      if (!data) return
      this.$http[this.submitFuncName](data).then(res => {
        if (res.data.retcode == 0) {
          this.$message.success('保存成功')
          this.$router.push({ path: '/information/list'})
        } else {
          this.$message.error('保存失败');
        }
      })
    },
    onSubmit () {
      this.$refs.form.validate()
      .then(valid => this.submit())
      .catch(() => {})
    },
    removeConverImageUrl () {
      this.form.image.id = this.form.image.url = ''
    },
    handleAvatarSuccess(data) {
      this.form.image.id = data.result_rows.id
      this.form.image.url = data.result_rows.url
    },
    beforeAvatarUpload(file) {
      const isImg = file.type.startsWith('image/');
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isImg) {
        this.$message.error('只允许上传图片');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isImg && isLt2M;
    }
  },
  created () {
    this.$http.pageList().then(res => {
      if (!res) return
      this.pageList.push(...res.data.result_rows.pages)
    })
    switch (this.$router.currentRoute.name) {
        case 'information_create':
          this.pageTitle = '添加咨讯'
          this.submitBtnTitle = '提交'
          this.submitFuncName = 'informationCreate'
          break
        default:
          this.pageTitle = '编辑咨讯'
          this.submitBtnTitle = '保存所有编辑结果'
          this.submitFuncName = 'informationUpdate'
          this.$http.InfomationDetail(this.$router.currentRoute.params)
            .then(res => {
              if (!res) return
              Object.assign(this.form, res.data.result_rows)
              if (this.form.link_type === 'page') this.form.pageId = this.form.link
              if (this.form.link_type === 'article') this.form.articleLink = this.form.link
              if (!this.form.image) this.form.image = { id: '', url: '' }
            })
      }
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
